import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import { hot } from 'react-hot-loader'
import md5 from 'md5'
import {
  Field,
  reduxForm,
} from 'redux-form/immutable'
import styles from './styles'

let captchaVal

const mobileRequired = (value) => (value ? undefined : '')
const mobileValid = (value) => (value && /^1[3|4|5|7|8][0-9]{9}$/.test(value) ? undefined : '')
const passwordRequired = (value) => (value ? undefined : '')
const passwordValid = (value) => (value && /^(?![a-zA-z]+$)(?!\d+$)(?![!@#$%^&*]+$)[a-zA-Z\d!@#$%^&*]+$/.test(value) ? undefined : '')

const verifyRequired = (value) => (value ? undefined : '')
const verifyValid = (value, allValues) => {
  const password = allValues.get('password')
  return value === password ? undefined : ''
}
const captchaRequired = (value) => (value ? undefined : '')
const captchaValid = (value, allValues, { captchaText }) => (value && md5(value) === captchaText ? undefined : '')
const msgValid = (value, allValues, props) => {
  const { captchaText, verifyCode } = props
  const captcha = allValues.get('captcha')
  if (!captcha) return ''
  if (md5(captcha) !== captchaText) return ''
  if (!value) return ''
  if (md5(value) !== verifyCode) return ''
  return undefined
}

class SignupForm extends React.PureComponent {
  static propTypes = {
    captchaTimer: PropTypes.number,
    captcha: PropTypes.string,
    captchaText: PropTypes.string,
    handleSubmit: PropTypes.func.isRequired,
    handleOnMessageClick: PropTypes.func.isRequired,
    handleOnCaptchaClick: PropTypes.func.isRequired,
  }

  state = {
    count: 60,
    isWaiting: false,
  }

  timer = undefined

  handleMessageClick = () => {
    if (this.timer !== undefined) return
    const phone = this.mobileEle.value
    const captcha = this.captchaEle.value
    if (!/^1[3|4|5|7|8][0-9]{9}$/.test(phone) || !captcha || md5(captcha) !== captchaVal) return
    clearInterval(this.props.captchaTimer)
    this.props.handleOnMessageClick(phone)
    this.setState({
      count: 60,
      isWaiting: true,
    })
    this.timer = setInterval(() => {
      let count = this.state.count
      if (--count === 0) {
        clearInterval(this.timer)
        this.timer = undefined
        this.setState({
          count: 60,
          isWaiting: false,
        })
      }
      this.setState({
        count,
      })
    }, 1000)
  }

  renderField = ({
    type,
    input,
    placeholder,
    meta: { touched, error }
  }) => (
    <div>
      <input {...input} placeholder={placeholder} type={type} />
      {touched &&
      (error &&
        <span className={styles.errMsg}>      <i className='fa fa-exclamation-circle' aria-hidden='true' />
          {error}
        </span>)}
    </div>
  )

  render() {
    const {
      captcha, captchaText, handleSubmit, handleOnCaptchaClick
    } = this.props
    const { count, isWaiting } = this.state
    captchaVal = captchaText
    return (
      <form className={styles.wrapper} onSubmit={handleSubmit}>
        <div className={styles.inputField}>
          <Field
            name='mobile'
            component={this.renderField}
            type='text'
            placeholder=''
            autocomplete='off'
            ref={(ele) => { this.mobileEle = ele }}
            validate={[mobileRequired, mobileValid]}
          />
        </div>
        <div className={styles.inputField}>
          <Field
            name='password'
            component={this.renderField}
            type='password'
            placeholder=''
            autocomplete='off'
            validate={[passwordRequired, passwordValid]}
          />
        </div>
        <div className={styles.inputField}>
          <Field
            name='verify'
            component={this.renderField}
            type='password'
            placeholder=''
            autocomplete='off'
            validate={[verifyRequired, verifyValid]}
          />
        </div>
        <div className={classnames(styles.inputField, styles.verification)}>
          <Field
            name='captcha'
            component={this.renderField}
            type='text'
            placeholder=''
            autocomplete='off'
            ref={(ele) => { this.captchaEle = ele }}
            validate={[captchaRequired, captchaValid]}
          />
          <div
            className={styles.captcha}
            dangerouslySetInnerHTML={{ __html: captcha }}
            onClick={handleOnCaptchaClick}
            onKeyDown={() => undefined}
            role='button'
            tabIndex={0}
          />
        </div>
        <div className={classnames(styles.inputField, styles.phoneCode)}>
          <Field
            name='code'
            component={this.renderField}
            type='text'
            placeholder=''
            autocomplete='off'
            // validate={[msgValid]}
          />
          <span
            className={classnames(styles.codeBtn, isWaiting ? styles.active : '')}
            onClick={this.handleMessageClick}
            onKeyDown={() => undefined}
            role='button'
            tabIndex={-1}
          >
            {!isWaiting ? '' : `${count}s`}
          </span>
        </div>
        <div className={styles.action}>
          <p><span></span></p>
        </div>
        <input
          type='submit'
          className={styles.actionBtn}
          value=''
        />
      </form>
    )
  }
}

const SignupReduxForm = reduxForm({
  form: 'login',
})(SignupForm)

const hotComponent = hot(module)(SignupReduxForm)

export default hotComponent



